import React, { useState } from 'react';

function WelcomeMessage({setProj, setFileName, setToggle, toggle}) {
  const [name, setName] = useState("");

  const handleSubmit = (e) => {
    e.preventDefault();
    setProj(true);
    setFileName(name);
  }

  return (
    <div className="fullHeight container containerCenter bgSecondary" onClick={(e) => toggle?setToggle(null):null}>
      <div className="flexCenter eightyWidth">
        <h2>
          Hey, I’m ChronoVis: Post-Englebart. In my world, <b>your left hand becomes the mouse, and your feet become the keyboard.</b>
        </h2><br/>
        <h2>
          I am a spin-off of ChronoVis: Paintbox, a no-code graphical interface for generating temporal data visualizations (built
          in collaboration with Dr. Johanna Drucker)
        </h2><br/>
        <h1>To begin, consult the README Pamphlet. Then, name and create a new project.</h1>
        <form className="container containerStretch flexRow" onSubmit={handleSubmit}>
          <input type="text"
            className="flexGrow2"
            placeholder="insert project name"
            value={name}
            onChange={(e) => setName(e.target.value)}
            required
          />&nbsp;&nbsp;&nbsp;
          <button className="buttonPrimary">Create Project</button>
        </form><br/><br/><br/><br/>
      </div>
    </div>
  );
}

export default WelcomeMessage;
